* {
  padding: 0;
  margin: 0;
}

.header {
  height: 70px;
}

.dropdown-menu {
  transform: translateX(-77%);
}

.cartitem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 20px;
  margin-bottom: 20px;
}

.cartItemImg {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  object-fit: cover;
}

.cartItemDetail {
  display: flex;
  flex: 1;
  padding: 0 20px;
  flex-direction: column;
}

/* Home Page */

.home {
  display: flex;
}

.filters {
  position: sticky;
  left: 0px;
  top: 70px;
  z-index: 1000;
  height: calc(100vh - 70px);
  width: 25vw;
  border: 2px solid #0095ff;
}

.title {
  font-size: 30px;
}

.sorts {
  text-align: left;
}

.productContainer {
  width: 75vw;
  margin: 0 auto;
}

/* Media Queries */
@media (max-width: 768px) {
  .filters {
    width: 100vw;
    position: absolute;
    height: auto;
    font-size: 12px;
    top: 70px;
  }

  .title {
    font-size: 18px !important;
  }

  .clearFilter {
    width: 95%;
  }

  .productContainer {
    width: 100vw;
    padding: 0;
    position: relative;
    top: 125px;
  }
}

@media (min-width: 1100px) {
  .filters {
    width: 20vw;
  }
  .productContainer {
    width: 80vw;
  }
}
